import { Routes } from '@angular/router';
import { CanDeactivateGuard } from '@backbase/contact-manager-widget-ang';
import { ContactManagerJourneyComponent } from './contact-manager-journey.component';
import { ContactManagerDetailViewComponent } from './views/contact-manager-detail/contact-manager-detail.component';
import { ContactManagerFormViewComponent } from './views/contact-manager-form/contact-manager-form.component';
import { ContactManagerTypeListViewComponent } from './views/contact-manager-type-list/contact-manager-type-list.component';

export const contactManagerRoutes: Routes = [
  {
    path: '',
    component: ContactManagerJourneyComponent,
    children: [
      {
        path: '',
        component: ContactManagerDetailViewComponent,
      },
      {
        path: 'select',
        component: ContactManagerTypeListViewComponent,
      },
      {
        path: ':id',
        component: ContactManagerDetailViewComponent,
      },

      {
        path: 'edit/:id/:type',
        canDeactivate: [CanDeactivateGuard],
        component: ContactManagerFormViewComponent,
      },
    ],
  },
];
